	module.exports = {"data":{"markdownRemark":{"html":"<p>The Security Page contains key information about your program and your security disclosure policy. The Security Page sets expectations of hackers you invite to your bounty program and it also outlines your:</p>\n<ul>\n<li>Disclosure policy</li>\n<li>Bug eligibility</li>\n<li>Policy for in-scope eligibility</li>\n<li>Bounty reward structure</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-e7322.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 59.018264840182646%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"security-page-3\"\n        title=\"\"\n        src=\"/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-e7ffa.png\"\n        srcset=\"/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-49813.png 125w,\n/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-dc8a5.png 250w,\n/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-e7ffa.png 500w,\n/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-dda24.png 750w,\n/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-c5f18.png 1000w,\n/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-22484.png 1500w,\n/static/security-page-3-b14019cb0dfa669c2170f0ffc09a788a-e7322.png 1752w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Completing the Security Page</h3>\n<p>To edit your Security Page, go to <strong>Settings > General > Information</strong>.</p>\n<p>A complete security page includes the following:</p>\n<ul>\n<li>Logo</li>\n<li>Cover Photo</li>\n<li>Overview</li>\n<li>Disclosure Policy</li>\n<li>Disclosure Rules</li>\n<li>Report Eligibility</li>\n<li>Reward Structure</li>\n<li>Policy</li>\n</ul>\n<p>It's important to keep your security page up-to-date so hackers always know the important details about your program. Check out the security pages for Yahoo!, Twitter, and Dropbox for reference.</p>\n<h3>Claiming the Security Page</h3>\n<p>Unless a security page is claimed, the page is curated by the HackerOne community. An organization can claim their security page in order to:</p>\n<ul>\n<li>Take editorial control of their disclosure policy</li>\n<li>Self-update their information</li>\n<li>Own their policy messaging</li>\n</ul>\n<p>You don't have to be a HackerOne customer in order to claim a Security Page, though signing up for a free HackerOne account is necessary.</p>\n<p>To claim a security page:</p>\n<ol>\n<li>Find an unclaimed security page in the directory.</li>\n</ol>\n<blockquote>\n<p><i>Note: Unclaimed pages have the <strong>Claim this page</strong> and the <strong>Suggest edits</strong> link.</i></p>\n</blockquote>\n<pre><code>![security-page-1](./images/security-page-1.png)\n</code></pre>\n<p><i>Claimed pages don't have the link available.</i></p>\n<pre><code>![security page-2](./images/security-page-2.png)\n</code></pre>\n<ol start=\"2\">\n<li>Click the <strong>Claim this page</strong> link.</li>\n<li>A HackerOne staff will either approve or reject your claim request. If your request is approved, you'll be given editing rights to the entire page.  </li>\n</ol>\n<p>HackerOne reviews each claim submission for validity before granting the claim. A security page can only be claimed once. Once you claim your security page, the community can no longer edit it.</p>\n<blockquote>\n<p><i>Note: Hackers looking to claim a page need to be above a certain reputation threshold to be given direct editing rights. If they don't have rights, they can suggest changes which will get reviewed and approved by HackerOne staff.</i></p>\n</blockquote>\n<h3>Suggesting Edits on the Security Page</h3>\n<p>You can suggest edits to any unclaimed security page by clicking on the <strong>Suggest edits</strong> link. A HackerOne staff will review your changes and approve them if they are valid.</p>","frontmatter":{"path":"/programs/security-page.html","id":"programs/security-page","title":"Security Page","description":null}}},"pathContext":{}}

